        "宁夏回族自治区":[106.169866,37.291332],
        "青海省":[96.043533,35.726403],
        "山东省":[118.187759,36.376092],
        "山西省":[112.304436,37.618179],
        "陕西省":[108.887114,35.263661],
        "上海市":[121.438737,31.072559],
        "天津市":[117.347043,39.288036],
        "西藏自治区":[88.388277,31.56375],
        "新疆维吾尔自治区":[85.294711,41.371801],
        "云南省":[101.485106,25.008643],
        "浙江省":[120.109913,29.181466],
      }
      // 数据
      var data = [
        { name: '北京市', value: 459},
        { name: '重庆市', value: 931},
        { name: '四川省', value: 4330},
        { name: '安徽省', value: 2912},
        { name: '福建省', value:  2808},
        { name: '甘肃省', value:  1725},
        { name: '广东省', value:  6115},
        { name: '广西壮族自治区', value: 2019 },
        { name: '贵州省', value: 2239 },
        { name: '海南省', value: 363},
        { name: '河北省', value: 3289 },
        { name: '河南省', value: 2813 },
        { name: '黑龙江省', value: 1145 },
        { name: '湖北省', value: 3149 },
        { name: '湖南省', value: 1659 },
        { name: '吉林省', value: 960 },
        { name: '江苏省', value: 5782 },
        { name: '江西省', value: 1425 },
        { name: '辽宁省', value: 2147 },
        { name: '内蒙古自治区', value: 5952 },
        { name: '宁夏回族自治区', value: 2007 },
        { name: '青海省', value: 887 },
        { name: '山东省', value: 5808 },
        { name: '山西省', value: 3734 },
        { name: '陕西省', value: 2616 },
        { name: '上海市', value: 957 },
        { name: '天津市', value: 775 },
        { name: '西藏自治区', value: 84 },
        { name: '新疆维吾尔自治区', value: 4578 },
        { name: '云南省', value: 3434 },
        { name: '浙江省', value: 4018 },
      ]
      var lineData = data.map(item => {
        return {
          coords: [
            center2[item.name],
            [center2[item.name][0], center2[item.name][1] + item.value * 0.0005]
          ]
        }
      })
      var scatterData = data.map(item => {
        return [center2[item.name][0], center2[item.name][1] + item.value * 0.0001]
      })
      var scatterData2 = data.map(item => {
        return center2[item.name]
      })
      var scatterData3 = data.map(item => {
        return center2[item.name].concat(item.name)
      })
      option = {
        backgroundColor: 'transparent',
        title: {
            text: '各地区发电总览',
            subtext:'单位：亿千瓦时',
            x:'10%',
            y:'2%',
            textStyle: {
                        fontSize:30,
                        color:'#ffd900',
                    },
            textAlign:'left'
          },
        // 地图图层样式
        geo: [{
          map: 'js1',
          aspectScale: 0.9,
          animationDurationUpdate:0,
          roam: true, //是否允许缩放
          //zoom: 1.1, //默认显示级别
          layoutSize: '120%',
          layoutCenter: ['50%', '58%'],
          itemStyle: {
            areaColor: 'transparent',
            borderColor: '#37C1FD',
            borderWidth: 2,
          },
          emphasis: {
            itemStyle: {
              areaColor: '#0160AD'
            },
            label: {
              show: 0,
              color: '#fff'
            }
          },
          zlevel: 3,
        }, {
          map: 'js2',
          aspectScale: 0.9,
          animationDurationUpdate:0,
          roam: true, //是否允许缩放
          //zoom: 1.1, //默认显示级别
          layoutSize: '120%',
          layoutCenter: ['50%', '58%'],
          itemStyle: {
            areaColor: {
              type: 'radial',
              x: 0.7,
              y: 0.5,
              r: 0.4,
              colorStops: [{
                offset: 0,
                color: '#56CCFB' // 0% 处的颜色
              }, {
                offset: 1,
                color: '#0160AD' // 100% 处的颜色
              }],
              global: false // 缺省为 false
            },
            borderColor: '#37C1FD',
            borderWidth: 2,
          },
          zlevel: 2,
          silent: true,
        }, {
          map: 'js3',
          aspectScale: 0.9,
          animationDurationUpdate:0,
          roam: true, //是否允许缩放
          //zoom: 1.1, //默认显示级别
          layoutSize: '120%',
          layoutCenter: ['50%', '60%'],
          itemStyle: {
            areaColor: '#005DDC',
            borderColor: '#329BF5',
            borderWidth: 1,
          },
          zlevel: 1,
          silent: true,
        },],
        toolbox: {
              show: true,
              orient: 'vertical',
              right: '0%',
              top: 'center',
              color:'#56a0d3',
              feature: {
                restore: {},
                saveAsImage: {},
              }
            },
        tooltip: {
              // 显示触发类型, item 在 hover 时显示
              geoIndex:1,
              trigger: 'item',
              formatter: '地区：{b}'
            },
        series: [
          // 画柱状图
          {
            type: 'lines',
            zlevel: 5,
            effect: {
              show: false,
              period: 10, //箭头指向速度，值越小速度越快
              trailLength: 0.02, //特效尾迹长度[0,1]值越大，尾迹越长重
              symbol: 'arrow', //箭头图标
              symbolSize: 5, //图标大小
            },
            tooltip:{
              triggerOn: 'mousemove',
              formatter:'地区'
            },
            lineStyle: {
              width: 15, //尾迹线条宽度
              color: {
                type: 'linear',
                x: 0,
                y: 2,
                x2: 1,
                y2: 0,
                colorStops: [{
                  offset: 0,
                  color: '#F09B0A' // 0% 处的颜色
                }, {
                  offset: 0.2,
                  color: '#F09B0A' // 0% 处的颜色
                }, {
                  offset: 0.5,
                  color: '#FEF03B' // 0% 处的颜色
                }, {
                  offset: 0.7,
                  color: '#FEF03B' // 0% 处的颜色
                }, {
                  offset: 1,
                  color: '#F09B0A' // 100% 处的颜色
                }],
                global: false // 缺省为 false
              },
              opacity: 1, //尾迹线条透明度
              curveness: 0 //尾迹线条曲直度
            },
            silent: true,
            data: lineData
          },
          
          // 柱形数量显示
          {
            type: 'scatter',
            coordinateSystem: 'geo',
            geoIndex: 0,
            zlevel: 5,
            label: {
              show: !0,
              position: 'right',
              formatter: params => data[params.dataIndex].value,
              padding: [2, 4],
              backgroundColor: '#003F5E',
              borderRadius: 2,
              borderColor: '#67F0EF',
              borderWidth: 0.5,
              color: '#67F0EF'
            },
            symbol: 'circle',
            symbolSize: [15, 8],
            itemStyle: {
              color: '#FEF03B',
              opacity: 0.8
            },
            silent: true,
            data: scatterData
          },
          
          // 图中圆点设置 
          {
            type: 'scatter',
            coordinateSystem: 'geo',
            geoIndex: 0,
            zlevel: 5,
            symbol: 'circle',
            symbolSize: [15, 8],
            itemStyle: {
              color: '#F09B0A',
              opacity: 0.6,
              shadowColor: '#000',
              shadowBlur: 5,
              shadowOffsetY: 2,
            },
            silent: true,
            data: scatterData2
          }, {
            type: 'effectScatter',
            coordinateSystem: 'geo',
            geoIndex: 0,
            symbol: 'circle',
            symbolSize: 4,
            showEffectOn: 'render',
            rippleEffect: {
              brushType: 'fill',
              scale: 10
            },
            hoverAnimation: true,
            label: {
              formatter: p => p.data[2],
              position: 'right',
              color: '#fff',
              fontSize: 14,
              distance: 10,
              show: !0,
            },
            itemStyle: {
              color: '#FEF134',
            },
            zlevel: 6,
            //data: scatterData3
          }]
      };
      myChart.setOption(option);

      // option && myChart.setOption(option);
      //捕捉georoam事件，使下层的geo随着上层的geo一起缩放拖曳
        myChart.on('georoam',function(params){
            var option = myChart.getOption();//获得option对象
            if(params.zoom!=null&&params.zoom!=undefined){ //捕捉到缩放时
                option.geo[2].zoom=option.geo[0].zoom;//下层geo的缩放等级跟着上层的geo一起改变
                option.geo[1].zoom=option.geo[0].zoom;
                option.geo[2].center=option.geo[0].center;//下层的geo的中心位置随着上层geo一起改变
                option.geo[1].center=option.geo[0].center;
            }else{//捕捉到拖曳时
                option.geo[2].center=option.geo[0].center;//下层的geo的中心位置随着上层geo一起改变
                option.geo[1].center=option.geo[0].center;
            }
            myChart.setOption(option);//设置option
        });
    }
  }
}
</script>

